import UI from '../config/ui.config';
const { submitButton } = UI;
const { dataListCoutrySignUp, selectCitySignUp } = UI.signUp;
function inputErrorTemplate(msg) {
    return `
    <div class="invalid-feedback">${msg}</div>
  `;
}
/**
 * Function showInputError. Add input error
 * @param {HTMLInputElement} el
*/
export function showInputError(el) {
    const parent = el.parentElement;
    if (parent.querySelector('.invalid-feedback'))
        return;
    const msg = el.dataset.invalidMessage || 'Invalid input';
    const template = inputErrorTemplate(msg);
    el.classList.add('is-invalid');
    parent.insertAdjacentHTML('beforeend', template);
    submitButton.disabled = true;
}
/**
 * Function removeInputError. Add remove error
 * @param {HTMLInputElement} el
*/
export function removeInputError(el) {
    const parent = el.parentElement;
    const err = parent.querySelector('.invalid-feedback');
    if (!err) {
        return;
    }
    el.classList.remove('is-invalid');
    parent === null || parent === void 0 ? void 0 : parent.removeChild(err);
    submitButton.disabled = false;
}
export function generateCountriesDataOptions(list) {
    Object.values(list).forEach((country) => {
        const template = `
      <option value="${country}"></option>
    `;
        dataListCoutrySignUp.insertAdjacentHTML('beforeend', template);
    });
}
export function generateCitiesOptions(list) {
    if (selectCitySignUp.disabled)
        selectCitySignUp.disabled = false;
    list.forEach((city) => {
        const template = `
      <option value="${city}">${city}</option>
    `;
        selectCitySignUp.insertAdjacentHTML('beforeend', template);
    });
}
